@import"https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap";*{margin:0;padding:0}body{background:#fff;color:#000;font-family:"Poppins",sans-serif;font-weight:300}.wrapper{width:1280px;margin:50px auto}header .logo{font-weight:600;font-size:40px;cursor:pointer}header .presentation{margin:50px auto;background:url("./img/shopClub.jpg") no-repeat;width:100%;height:500px;background-size:cover;background-position:center center;background-blend-mode:multiply;position:relative}header .presentation::after{content:"Лучшая одежда для вас";position:absolute;background-color:rgba(0,0,0,.3);padding-left:50px;top:100px;left:0px;width:300px;font-size:40px;font-weight:600;color:#fff}header .presentation::before{content:"По низким ценам";position:absolute;background-color:rgba(0,0,0,.3);padding-left:50px;top:300px;left:0px;width:300px;font-size:20px;font-weight:300;color:#fff}header ul.nav{margin:20px auto;float:right;list-style:none}header ul.nav li{display:inline;margin-left:25px;cursor:pointer;transition:opacity 500ms ease}header ul.nav li:hover{opacity:.7}main{display:flex;width:100%;flex-wrap:wrap;justify-content:space-between}main .item{width:30%;margin-bottom:50px}main .item img{width:100%;border-radius:10px 10px 0px 0px;height:500px}main .item img:hover{transform:scale(1.05)}footer{text-align:center;padding-top:10px}.bestsellers_title{text-align:center;margin:30px 0px}